<template>
  <div class="staff-list">
    <div class="header">
      <div class="top">
        <span class="iconfont icon-jiantou" @click="onClickLeft"></span>
        <h3>员工列表</h3>
      </div>
    </div>
    <div class="staff-wrap">
      <div v-for="(item, index) in list"

   :key="index"
       class="one-staff clearfix">
        <h6 class="name inaline">{{item.userName}}</h6>
        <!-- <p>职务: {{item.remark}}</p> -->
        <p>电话: {{item.phonenumber}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { queryStaff } from '@/api/index';


export default {
  data() {
    return {
      list: []
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  },
  created() {
    queryStaff().then(res => {
      if (res) {
        if (res.data.code === 0) {
          this.list = res.data.data;
        } else {
          this.$Toast(res.data.msg);
        }
      }
    });
  }
};
</script>

